.user-nav {
  min-height: 100%;
  z-index: 1;
  position: absolute;
  top: 59px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: @white;
  height: 50px;

  @media (min-width: @desktop-width) {
    display: block;
    position: static;
  }
}

@media (max-width: 1239px) {
  .navigation--closed .user-nav {
    display: none;
  }
}

.user-nav__list {
  display: flex;

  @media (min-width: @tablet-width) {
    // margin-left: auto;
    color: @basic-green;
  }
}

.user-nav__list--disabled {
  display: none;
}

.user-nav__link {
  display: block;
  color: inherit;

  @media (min-width: @tablet-width) {
    padding: 10px 10px 10px 10px;
  }

  @media (min-width: @desktop-width) {
    line-height: 20px;
    padding: 18px 10px 19px 10px;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }
}

.user-nav__link--reg {
  position: relative;

  @media (min-width: @tablet-width) {
    padding-right: 0;
    &::before {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      width: 1px;
      height: 17px;
      transform: translateY(-50%);
      background-color: @basic-green;
    }
  }
}
