.navigation--mobile {
  order: -1;

  @media (min-width: @desktop-width) {
    order: 0;
  }
}

.navigation__button-login {
  width: 100%;
  padding: 12px 0 13px 20px;
  border-bottom: 1px solid @light-gray;
  align-items: start;
}

@media (max-width: 1239px) {
  .navigation--closed .main-nav {
    display: none;
  }
}

.navigation__login-text {
  font-size: 15px;
  line-height: 28px;
  float: left;
}

// Nav
